
.journal__main-content{
    display: flex;
}

.journal__sidebar {
    background-color: $dark_grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}
.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;
}

.journal__sidebar-navbar h3{

    font-weight: lighter;
}

.journal__new-entry {

    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 100%;
    margin-top: 30px;
    flex-direction: column;
    transition: color .3s ease;

    &:hover{
        color: darken($color: white, $amount: 20);
    }
}

.journal__entries{
    flex: 1 1 auto;
    margin-top: 30px;
    overflow: scroll;

}
.journal__entries::-webkit-scrollbar {
    display: none;
  }

.journal__entry{

  background-color: white;
  border-radius: 4px;
  color: $dark_grey;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  overflow: hidden;
}

.journal__entry-pictures{
   
 
    width: 75px;
    height: 75px;
}

.journal__entry-body{
 
    padding:10px;
    
}
.journal__entry-title{

    font-size: 14px;
    font-weight: bold;

}
.journal__entry-content {
   font-size: 10px;
}
.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px
}
.journal__entry-date-box span{
    font-size: 12px;
}